import React from 'react';
import CircleSelector from './CircleSelector/CircleSelector'
import Circles from './Circles/Circles'
import './App.css';
import { useState } from 'react';

const circles = ['1', '2', '3', '4']


function App() {
  const [selCircleIdx, setSelCircleIdx] = useState(0)

  const handleCircleSelection = (circleIdx) => {
    setSelCircleIdx(circleIdx)
  }

  return (
    <div className="App">
    <header className="App-header">UNIT 4 FINAL ASSESSMENT</header>
    <main>

      <CircleSelector
            circles={circles}
            selCircleIdx={selCircleIdx}
            handleCircleSelection={handleCircleSelection}
          />

      <Circles
            circles={circles}
            selCircleIdx={selCircleIdx}
          />
          
    </main>
    </div>
  );
}

export default App;
